<template>
  <div class="straightBasket">
    <v-container fluid>
      <v-layout
        row
        wrap
        class="p-0 my-0">
        <v-flex
          md8
          xs12
          tag="h3"
          class="my-0 pb-3">
          {{ $ln(gname) }} - {{ $ln('單式') }}
        </v-flex>

        <!-- 每秒更新 -->
        <CommonCounter
          :reciprocalItems.sync="reciprocalItems"
          :updateTableList="updateTableList"
        />
      </v-layout>

      <v-layout
        row
        wrap
        class="my-0">
        <SportBallStatus
          :dataSelect.sync="selectItems.selected"
          :items="selectItems.options"
          title="開放中＋關閉中"
          class="selectHide"
        />

        <!-- <SportBallStatus
          :dataSelect.sync="selectGameItems.selected"
          :items="selectGameItems.options"
          title="玩法："
        /> -->

        <SportBallStatus
          :dataSelect.sync="selectChangeItems.selected"
          :items="selectChangeItems.options"
          title="拆帳"
        />

        <v-flex
          md3
          xs6
          class="px-1">
          <v-layout
            row
            wrap>
            <SportBallStatus
              classType="classType"
              :dataSelect.sync="selectRowsPerPageItems.selected"
              :items="selectRowsPerPageItems.options"
              title="每頁顯示"
            />
          </v-layout>
        </v-flex>
        <v-flex
          xs3
          md2
          class="grey--text text--darken-2 d-flex align-center">
          {{ $ln(`共 %s 筆`, pagination.totalItems) }}
        </v-flex>
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else class="pt-1">
        <!-- 一般玩法 -->
        <SportStraightTable
          v-if="tabledata1.show"
          :header.sync="tabledata1.headers"
          :contents.sync="sportsList"
          :path="path"
          :detailType="detailType"
          :detailGame="detailGame"
          gameType="index"
        />

        <div class="text-xs-center pt-2">
          <v-pagination
            v-model="pagination.currentPage"
            :length="pages"
            color="deep-orange darken-1"
            @input="loadNextPage"/>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import Straight from '@/mixins/sport/Straight.vue'

export default {
  name: 'Straight',
  mixins:[Straight],
  data () {
    return {
      gname: '指數',
      // 連結注單明細
      path: '/manage/sport/index/CheckView',
      // 明細-注單玩法參數
      detailType: '2,4',
      // 明細-game參數 ?API文件未看到該參數?
      detailGame: '',
      // 一般玩法
      tabledata1: {
        show: false,
        // showNoData: '目前無資料',
        headers: {
          // 'league_name': { text: '聯盟名稱', align: 'center', sortable: false }, // 已被獨立成一行
          'count_date': { text: '帳務日期', align: 'center', sortable: false },
          'game_time': { text: '時間', align: 'center', sortable: false },
          'stock': { text: '股市名稱', align: 'center', sortable: false },
          'BS': { text: '大小', align: 'center', sortable: false },
          'SD': { text: '單雙', align: 'center', sortable: false },
          'detail': { text: '明細', align: 'center', sortable: false }
        }
      },
      // 單式球類傳入參數
      gamelist: {
        ball: 11
      }
    }
  },
}
</script>
